:root {
	--mainEng-font: "Montserrat";
	--mainKor-font: "pretendard";
	--mainNum-font: "gmarket";

	--mainBg-color: #eeeeee;
	--subBg100: #cecece;
	--subBg200: #b0b0b0;
	--subBg300: #a0a0a0;
	--subBg400: #c7c7c7;
	--subBg500: #bcbcbc;

	--white: #fff;
	--black: #000;
	--black100: #2b2b2b;
	--black200: #434343;
	--black300: #686868;
	--black400: #e0e0e0;

	font-family: var(--mainEng-font), var(--mainKor-font);
	font-size: 16px;
	line-height: 1.5;
	font-weight: 400;

	font-synthesis: none;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	-webkit-text-size-adjust: 100%;

	@media (max-width: 800px) {
		font-size: 14px;
		line-height: 1.4;
	}
}

body {
	background-color: var(--mainBg-color);
}
